.poster{
    width: 100%;
    height: 80vh;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem 0;
}

.poster .image{
    display: flex;
    flex-direction: row;
    position: relative;
    width: 60%;
    height: 100%;
}

.poster .image img{
    width: 50%;
    margin-right: 20px;
}

.poster .image .title img{
    height: 35%;
    width: 75%;
}

.poster .image .title p{
    font-size: .6rem;
    font-weight: 500;
}

.poster .image a{
    background-color: #FB641B;
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    bottom: 20%;
    right: 25%;
    width: 250px;
    height: 50px;
    font-size: 14px;
    text-decoration: none;
    color: white;
    border-radius: 3px;
    /* padding: 0 40px 0 20px; */
}

.poster .image a .icon{
    font-size: 25px;
    margin-right: 10px;
    /* margin: 0 1rem; */
}

@media (max-width: 768px){

    .poster{
        height: 30vh;
        width: 100%;
    }

    .poster .image a{
        height: 40px;
        width: 150px;
        /* padding-right: 0; */
        font-size: 12px;
    }

}

@media (max-width: 468px){

    .poster{
        height: 42vh;
        padding-right: 10px;
    }

    .poster .image{
        width: 90%;
    }

    .poster .image a{
        height: 40px;
        width: 180px;
        /* padding-right: 0; */
        font-size: 14px;
    }

    .poster .image .title p{
        font-size: .8rem;
    }

    .poster .image a .icon{
        font-size: 17px;
        /* margin-right: 5px; */
    }
}

@media (max-width: 360px){

    .poster .image a{
        /* font-size: 7px; */
        overflow: hidden;
    }

    .poster .image a .icon{
        font-size: 12px;
    }

}
